/* src/index.css */
@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --foreground-rgb: 230, 230, 255;
  --background-start-rgb: 26, 26, 46;
  --background-end-rgb: 26, 26, 46;
}

body {
  color: rgb(var(--foreground-rgb));
  background: linear-gradient(
          to bottom,
          transparent,
          rgb(var(--background-end-rgb))
  )
  rgb(var(--background-start-rgb));
}

/* Game log text colors */
.success {
  color: #66ff66;
}

.failure {
  color: #ff6666;
}

.critical {
  color: #ffcc00;
  font-weight: bold;
}

/* Custom scrollbar for the game log */
.overflow-y-auto::-webkit-scrollbar {
  width: 8px;
}

.overflow-y-auto::-webkit-scrollbar-track {
  background: rgba(37, 37, 71, 0.5);
  border-radius: 4px;
}

.overflow-y-auto::-webkit-scrollbar-thumb {
  background: rgba(77, 77, 255, 0.5);
  border-radius: 4px;
}

.overflow-y-auto::-webkit-scrollbar-thumb:hover {
  background: rgba(77, 77, 255, 0.7);
}

/* Custom animations */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.animate-fadeIn {
  animation: fadeIn 0.5s ease-in-out;
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.05); }
  100% { transform: scale(1); }
}

.animate-pulse-slow {
  animation: pulse 2s infinite;
}

/* Mobile-specific styles - これらは !important を使わないようにしています */
@media (max-width: 768px) {
  body {
    font-size: 14px;
  }

  h1 {
    font-size: 1.75rem;
  }

  h2 {
    font-size: 1.5rem;
  }

  h3 {
    font-size: 1.25rem;
  }

  /* Add more bottom padding to account for fixed navigation */
  main {
    padding-bottom: 4rem;
  }

  /* Make buttons bigger on touch devices */
  button, .btn {
    min-height: 2.5rem;
    padding: 0.5rem 1rem;
  }

  /* Game UI specific mobile styles */
  .game-page, .town-page {
    padding: 0.5rem;
  }

  /* Mobile grid overrides */
  .grid {
    gap: 0.75rem;
  }

  /* Bottom navigation spacing */
  .pb-navigation {
    padding-bottom: 4rem;
  }
}

/* Touch-friendly adjustments */
@media (hover: none) {
  /* Custom active states for touch */
  button:active, .btn:active {
    transform: scale(0.98);
    opacity: 0.9;
  }
}

/* Bottom navigation container */
.bottom-nav {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(30, 27, 75, 0.95);
  backdrop-filter: blur(4px);
  border-top: 1px solid rgba(79, 70, 229, 0.3);
  padding: 0.5rem;
  display: flex;
  justify-content: space-around;
  z-index: 50;
  box-shadow: 0 -4px 6px -1px rgba(0, 0, 0, 0.1), 0 -2px 4px -1px rgba(0, 0, 0, 0.06);
}

/* Bottom nav buttons */
.bottom-nav-btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  transition: all 0.2s;
}

.bottom-nav-btn:active {
  background-color: rgba(79, 70, 229, 0.3);
}

.bottom-nav-label {
  font-size: 0.75rem;
  margin-top: 0.25rem;
}

/* Quest modal styles */
.quest-modal {
  position: fixed;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.75);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100;
  padding: 1rem;
}

.quest-modal-content {
  background-color: #1e1b4b;
  border-radius: 0.5rem;
  width: 100%;
  max-width: 28rem;
  max-height: 80vh;
  overflow-y: auto;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}
/* src/index.css に追加 */

/* 固定高さのフッター関連スタイル */
.fixed-footer {
  height: 4rem; /* 64px */
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.footer-button {
  height: 2.5rem; /* 40px */
  min-height: 2.5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* コンテンツのパディングを統一 */
.pb-footer {
  padding-bottom: 4rem; /* フッターと同じ高さ */
}

/* レスポンシブレイアウト用 */
@media (max-width: 768px) {
  /* モバイル時のヘッダーとフッターのスペースを確保 */
  .page-container {
    padding-top: 1rem;
    padding-bottom: 2rem;
  }

  /* ボタンサイズの統一 */
  .button, button, .btn {
    height: 2.5rem;
    min-height: 2.5rem;
  }
}

/* 共通のコンテナパディング */
.container-padding {
  padding-left: 1rem;
  padding-right: 1rem;
  padding-bottom: 2rem;
}
